import React from 'react'
import './Footer.css'
import '../css/media.css'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faDiscord, faTwitter } from '@fortawesome/free-brands-svg-icons';





const Footer = () => {
  return (
    <div>
      <div className="footer-box">
        <div className="footer-box2">
          <div className="footer-box3">
            <div className="footer-box4">
              <div className="footer-box5">
                <p className="footer-p media-footer-p">©2023,BabyBonk NFTs</p>
                <div className="footer-box6">
                  <a rel="noreferrer" target="_blank" href="https://discord.gg/Babybonk" className="footer-a"><FontAwesomeIcon className='icon-community' icon={faDiscord} /></a>
                  <a rel="noreferrer" target="_blank" href="https://twitter.com/Baby_bonkinu" className="footer-a"><FontAwesomeIcon className='icon-community' icon={faTwitter} /></a>
                </div>
                <p className="footer-p media-footer-p">To conquer the sea of ​​solana</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  )
}

export default Footer
